  padding: 0;
  margin: 0;
  font-size: 1.1rem;
  letter-spacing: 1.2px;
  box-sizing: border-box;
  /* border: 2px solid teal; */
}

body {
  display: flex;
  flex-direction: column;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  font-family: 'Raleway', sans-serif;
}

dialog section {
  width: 400px;
  padding: 20px;
  background-color: rgb(194, 146, 79);
}

div {
  display: flex;
  flex-direction: column;
}

input {
  flex: 1 1 auto;
  font-size: 1rem;
  padding-left: 5px;
}

form {
  margin: 100px;
}

fieldset {
  display: flex;
  justify-content: center;
  padding: 50px;
  border: 2px solid rgb(163, 163, 163);
}

li {
  list-style-type: none;
}

.hideForm,
body > button,
.cancelForm {
  font-size: 1rem;
  margin: 30px 0;
  padding: 0.4rem;
  color: black;
  background-color: rgb(212, 190, 160);
  border-radius: 0.7rem;
  cursor: pointer;
}

.cancelForm {
  background-color: rgb(255, 255, 255);
}

.formButtons {
  flex-direction: row;
  justify-content: space-between;
}

.hideForm:hover,
body > button:hover,
.cancelForm:hover {
  color: rgb(66, 66, 66);
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.4);
}

body > button {
  margin: 30px auto;
}

dialog {
  margin: auto;
  border: none;
  background-color: rgba(209, 234, 250, 0);
}

dialog + section {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  margin: auto;
  padding: 1.4rem;
  flex-wrap: wrap;
  gap: 1rem;
  width: 700px;
  height: 700px;
  background-color: rgb(212, 190, 160);
  border: 5px solid rgb(194, 146, 79);
  border-radius: 2rem;
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.4);
}

.last-sec > div {
  display: flex;
  justify-content: space-around;
  text-align: center;
  width: 200px;
  height: 200px;
  padding: 0 5px;
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.3);
  background-color: rgb(194, 146, 79);
  cursor: context-menu;
  transition: transform 0.2s ease-in-out;
}

.last-sec > div:hover {
  transform: scale(1.1);
  z-index: 1;
}

h1 {
  margin: 20px auto;
  font-size: 2rem;
  color: white;
  cursor: context-menu;
}

section + p {
  margin: 20px auto;
  padding: 10px;
  background-color: rgb(194, 146, 79);
}

fieldset {
  border: 2px solid rgba(0, 0, 0, 0.4);
}

.last-sec button {
  width: 50px;
  /* margin: auto; */
  font-size: 0.8rem;
  padding: 2px;
  color: black;
  background-color: rgb(212, 190, 160);
  cursor: pointer;
}

.last-sec > div > div {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
